*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #61305d;
  position: relative;
}
html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
}

.title {
  margin-left: 15%;
  color: #fff;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.icons-nav {
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  width: 70px;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  overflow-y: auto;
  background-color: #e77c7c;
  .chart-type {
    color: #fff;
    background-color: #b55c6c;
    border-radius: 100%;
    padding: 10px;
    margin: 25%;
    cursor: pointer;
    transition: color 200ms;
    scrollbar-color: #a66c7c;
    &:hover {
      color: black;
    }
  }
}

.graph {
  width: 700px;
  max-width: 90%;
  margin-left: 80px;
  margin-right: 50%;
  margin-top: 20px;
}

.cards {
  margin: 0 90px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80%;
  margin-top: 15px;
  margin-bottom: 100px;
  .card {
    max-width: 20%;
    min-height: 220px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: orangered;
    color: white;
    border-radius: 15px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
    .card-title {
      text-align: center;
      font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
    }
    .card-body {
      max-width: 80%;
      font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
        "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    }
  }
}

@media only screen and (max-width: 800px) {
  .cards {
    flex-direction: column;
    .card {
      max-width: 50%;
      margin: 15px 0;
    }
  }
}

@media only screen and (max-width: 750px) {
  .graph {
    width: 350px;
  }
}

@media only screen and (max-width: 560px) {
  .title {
    display: none;
  }
}
